export default () => {
	const btnOpen = document.querySelector('.sidenav__btn');
	const btnClose = document.querySelector('.sidenav__close');
	const overlay = document.querySelector('.sidenav__overlay');
	const body = document.querySelector('body');
	const page = document.querySelector('html');
	const wrap = document.querySelector('.sidenav__body');
	const activeClass = 'page_open-side';
	const appVersion = navigator.appVersion;
	const isAndroid = (/android/gi).test(appVersion);
	const isIOS = (/iphone|ipad|ipod/gi).test(appVersion);
	const probablyMobile = (isAndroid || isIOS || /(Opera Mini)|Kindle|webOS|BlackBerry|(Opera Mobi)|(Windows Phone)|IEMobile/i.test(navigator.userAgent));
	let scrollbarSize;

	const _getScrollbarSize = function() {
		// thx David
		if(scrollbarSize === undefined) {
			const scrollDiv = document.createElement("div");
			scrollDiv.style.cssText = 'width: 99px; height: 99px; overflow: scroll; position: absolute; top: -9999px;';
			document.body.appendChild(scrollDiv);
			scrollbarSize = scrollDiv.offsetWidth - scrollDiv.clientWidth;
			document.body.removeChild(scrollDiv);
		}
		return scrollbarSize;
	};

	const _hasScrollbar = function() {
		var d = document,
			b = d.body,
			e = d.documentElement,
			c = "client" + 'Height';
			a = "scroll" + 'Height';

		const hasScroll = /CSS/.test(d.compatMode) ? (e[c]< e[a]) : (b[c]< b[a]);
		return hasScroll;
	};

	const open = () => {
		const scroll = (_hasScrollbar()) ? _getScrollbarSize() : 0;
		if (probablyMobile) {
			page.css({
				'margin-right' : scroll,
				'overflow' : 'hidden'
			});
		} else {
			page.wrap.css({
				'overflow' : 'hidden'
			});
		};
		body.classList.add(activeClass);
	};

	const close = () => {
		body.classList.remove(activeClass);
	};

	btnOpen.addEventListener('click', open);
	overlay.addEventListener('click', close);
	btnClose.addEventListener('click', close);

	/*(function() {
		Nav = function(target) {
			function t(target) {
				this.status = false;
				this.overlay = $('.overlay');
				this._menuTimer = void 0;
				this.body = $('#'+target);
				this.closeBtn = this.body.find('.side__btn');
				this.menuButton = $('.link-modal[href=' + target + ']');
			};
			return t.prototype._initEvents = function() {
				return this.menuButton.on(Event.CLICK, this._onMenuButtonClick),
				this.overlay.on(Event.CLICK, this._onNavOverlayClick)
			},
			t.prototype._onMenuButtonClick = function() {
				return this.status === false ? this.openMenu() : this.closeMenu()
			},
			t.prototype._onNavOverlayClick = function() {
				return this.status === true ? this.closeMenu() : void 0
			},
			t.prototype.openMenu = function(){
				this.status = true;
				this._savedScrollTop = $(window).scrollTop();
				var s = (this._hasScrollbar()) ? this._getScrollbarSize() : 0;
				if (!this.probablyMobile) {
					this.helper.css({
						'right' : s
					});
					this.wrap.css({
						'margin-right' : s,
						'overflow' : 'hidden'
					});
				} else {
					this.wrap.css({
						'overflow' : 'hidden'
					});
				};

				this.overlay.css('display', 'block');
				this.body.css('display', 'block');
				this.overlay.addClass('showed');
				this.wrap.css({
					'overlay' : 'hidden'
				});
				this._menuTimer = setTimeout(function(t) {
					return function() {
						return t.body.addClass("showed")
					}
				}(this), 100);
				$('.form__file').trigger('refresh');
			},
			t.prototype.closeMenu = function(){
				this.status = false;
				this.overlay.css('display', 'none');
				this.body.css('display', 'none');
				if (!this.probablyMobile) {
					this.wrap.css({
						'margin-right' : '0',
						'overflow' : 'auto'
					});
					this.helper.css({
						'right' : '0'
					})
				} else {
					this.wrap.css({
						'overflow' : 'auto'
					});
				};
				this.overlay.removeClass('showed');
				this.body.removeClass('showed filled');
			},
			t;
		}();*/
};


